import React from "react";
import author from "../img/avatar.png";

const AboutMe = () => {
  return (
    <div id="about" className="container-fluid py-5">
      <div className="row">
        <div className="col-lg-6 col-xm-12">
          <div className="photo-wrap mb-5">
            <img className="profile-img" src={author} alt="author..." />
          </div>
        </div>
        <div className="col-lg-6 col-xm-12">
          <h1 className="about-heading">Sobre mim</h1>
          <p>
            Ol, me chamo talo, desenvolvo Websites e aplicaes Web desde
            2022. Sou um desenvolvedor Front-End. Atualmente utilizo algumas
            tecnologias como ReactJS, VueJS, Bootstrap e AngularJS.
          </p>
          <p>
            Crio Websites responsivos que podem ser acessados em diversos
            dispositivos como desktops e smartphones.
          </p>
          <p>
            Antes de comear o desenvolvimento de um Webapp, Landing Page,
            Business Website ou E-commerce,  preciso criar um layout de projeto
            (sketch).
          </p>
          <p>
            E estou pronto para faz-lo por voc, antes de comearmos a
            desenvolver seu projeto, iremos discutir todos os detalhes, como seu
            pblico alvo, objetivo e detalhes especficos de cada etapa do
            projeto. Avaliaremos as vantagens e desvantagens de seus
            competidores em relao ao seu produto.
          </p>
          <p>
            Tudo isso  necessrio para dar a seus clientes algo que seus
            competidores no apresentam, ou apresentar o mesmo, porm de maneira melhorada!          
          </p>          
        </div>
      </div>
    </div>
  );
};

export default AboutMe;
